:root {
  --primary: #161616;
  --secondary: #878686;
  --backgroundMain: #ffffff;
  --backgroundOther: #f3f3f3;
  --colored: #DF1748;
}

body {
  margin: 0 !important;
  padding: 0 !important;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
  user-select: none;
  background-color: var(--backgroundMain);
  transition: background-color 0.4s ease;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
  padding: 16px;
}

#subheading p {
  font-size: 46pt;
  font-weight: bold;
  color: var(--secondary);
  line-height: 0.8;
  margin-top: 10px;
  transition: color 0.4s ease;
}

#colored {
  color: var(--colored);
}

#heading p {
  letter-spacing: -0.04em;
  font-size: 82pt;
  font-weight: bold;
  color: var(--primary);
  line-height: 0.8;
  transition: color 0.4s ease;
}

#description p {
  font-size: 18pt;
  font-weight: bold;
  color: var(--secondary);
  transition: color 0.4s ease;
}

#description {
  margin-top: 50px;
  margin-bottom: 50px;
}

#select {
  font-size: 20pt;
  font-weight: bold;
  padding: 16px 70px;
  border-radius: 26px;
  background-color: var(--primary);
  color: var(--backgroundMain);
  transition: color 0.4s ease;
}

#select:active {
  transform: scale(0.98);
  background-color: var(--colored);
  transition: background-color 0.4s ease;
}

#clear {
  display: none;
}

#swatches {
  margin-top: 10px;
}

.fullstop {
  opacity: 0;
}
